<div :class="[       isMobile ? 'w-screen h-auto' : 'w-auto h-[100vh] border-r-2',     ]"><div v-if="!isMobile &amp;&amp; (isMobile !== null)"><div class="w-auto  p-2"><el-button class="w-full" :icon="Switch" @click="isCollapse = !isCollapse"></el-button></div><el-menu :default-active="defaultActive" :collapse="isCollapse" background-color="#ffffff" @open="handleOpen" @close="handleClose" style="border-right: 0px;"><el-menu-item class="w-[200px]" v-for="(item, index) of nowMenu" :key="index" :index="index + &quot;&quot;" @click="toLink(item)"><el-icon><component :is="item.icon"></component></el-icon><template #title>{{item.name}}</template></el-menu-item></el-menu></div><div v-else-if="isMobile !== null"><div class="w-screen h-[80px] bg-gradient-to-b from-[#ffb6c1] flex items-center justify-around"><div class="w-[60px]" v-for="(item, index) of nowMenu" :key="index" :class="[           ((nowChose === item.route) || (nowChose === item.query)) ? 'scale-125' : '',         ]" @click="toLink(item)"><el-icon :size="28"><component :is="item.icon"></component></el-icon><div class="text-xs">{{item.name}}</div></div></div></div></div>